import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { HttpClientModule } from '@angular/common/http';
import { RouterModule, Routes} from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { AboutComponent } from './about/about.component';
import { FooterComponent } from './footer/footer.component';
import { MenuComponent } from './menu/menu.component';
import { SaveContactComponent } from './save-contact/save-contact.component';
import { CreateContactComponent } from './create-contact/create-contact.component';
import { DetailContactComponent } from './detail-contact/detail-contact.component';
import { ContactsComponent } from './contacts/contacts.component';
import { SnipperComponent } from './snipper/snipper.component';
import { UpdateContactComponent } from './update-contact/update-contact.component';
import { ContactsService } from  'src/services/contacts.service';
import { ArchitectureComponent } from './architecture/architecture.component';


const APPROUTES: Routes = [

  {path: 'about', component:AboutComponent},
  {path: '',
  redirectTo: '/about', pathMatch: 'full'},
  
  {path: 'createContact/:id', component:CreateContactComponent},
  {path: 'createContact', component:CreateContactComponent},
  {path: 'snipper', component:SnipperComponent},
  
  {path: 'contacts', component: ContactsComponent},
  {path: 'contacts/:keyword', component: ContactsComponent},
  {path: 'detail/:id', component:DetailContactComponent},
  {path: 'updateContact', component: UpdateContactComponent},
  {path: 'updateContact/:id', component: UpdateContactComponent},
  {path: 'architecture', component:  ArchitectureComponent},
 
  {path: 'contact', component:FooterComponent}
]



@NgModule({
  declarations: [
    AppComponent,
    
    AboutComponent,
    
    FooterComponent,
    
    MenuComponent,
    
    SaveContactComponent,
    
    CreateContactComponent,
    
    DetailContactComponent,
    
    ContactsComponent,
    
    SnipperComponent,
    
    UpdateContactComponent,
    
    ArchitectureComponent,
    
    
  ],
  imports: [
    BrowserModule, HttpClientModule, RouterModule.forRoot(APPROUTES),
    FormsModule, ReactiveFormsModule
  ],
  providers: [ContactsService],
  bootstrap: [AppComponent]
})
export class AppModule { }
