export class Contact {
	/**
	 * 
	 */
	
	
	public id : any = null;
	public nom : string = '';
	public  prenom : string = '';
	
	public  dateNaissance: Date;
	public email : string = '';
	public  phone : string = '';
	public photo : string = '';
	
	public Contact() {
		// TODO Auto-generated constructor stub
	}

	public  getNom(): string {
		return this.nom;
	}

	public  setNom( nom : string): any {
		this.nom = nom;
	}

	public  getPrenom() : string {
		return this.prenom;
	}

	public  setPrenom( prenom : string) : any{
		this.prenom = prenom;
	}

	public  getDateNaissance() :Date{
		return this.dateNaissance;
	}

	public  setDateNaissance(dateNaissance : Date): any {
		this.dateNaissance = dateNaissance;
	}

	public getEmail() : string {
		return this.email;
	}

	public setEmail( email : string) {
		this.email = email;
	}

	public  getPhone(): string {
		return this.phone;
	}

	public setPhone(phone : string) {
		this.phone = phone;
	}

	public  getPhoto() : string{
		return this.photo;
	}

	public setPhoto(photo : string) {
		this.photo = photo;
	}

	public  getId(): number {
		return this.id;
	}

	public  setId( id: number) {
		this.id = id;
	}
	
	
}